/*
 *   Name: Bizagi Workportal Desktop Project Timeline Controller
 *   Author: Diego Parra
 */

bizagi.workportal.widgets.project.base.extend("bizagi.workportal.widgets.project.timeline", {}, {
    /*
     *   Constructor
     */
    init: function(workportalFacade, dataService, params) {

        var self = this;

        if(params.data){
            $.extend( params, params.data );
        }

        //Call base
        self._super(workportalFacade, dataService, params);

        //Create hashtables to hold user names and pictures
        self.usersByName = {};
        self.userPictures = {};
        self.usersInformation = [];

        //Show Current Date flag
        self.showCurrentDate = params.showCurrentDate || false;

        // Create a slot to hold timeline data and avoid going to server after filtering
        self.timelineData = null;

        self.auxNoRepeatTypesUser = {};
        self.hashTypesUser = {
            "assigned": bizagi.localization.getResource("workportal-project-user-assigned"),
            "owner": bizagi.localization.getResource("workportal-project-user-owner")
        };

        self.numberOfResults = 0;
        //Load templates
        self.loadTemplates({
            "project-timeline-markup": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.timeline").concat("#project-timeline-markup"),
            "project-timeline-empty-process": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.timeline").concat("#project-timeline-empty-process"),
            "project-timeline-single-process": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.timeline").concat("#project-timeline-single-process"),
            "project-timeline-activity-assignee": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.timeline").concat("#project-timeline-activity-assignee"),
            "project-timeline-empty-results": bizagi.getTemplate("bizagi.workportal.desktop.widget.project.timeline").concat("#project-timeline-empty-results")
        });

    },

    /**
     * show the usersModal for this activity}
     * @param {widget.project.timeline} self is the this reference of the class, in this method this is and eventElement
     * @param {Event} e event element
     */
    showMoreUsersModal: function(self, e){
        var idWorkitem = $(e.target).closest(".bz-project-timeline-content").data("workitem");
        $.when(self.dataService.getItemsAssignees(idWorkitem)).done(function (userListAItems) {
            self.getUsersForUnassignedActivityModal(self, userListAItems).done(function(usersForModal) {
                var task = self.timelineData.events.find(function(ev){
                    return ev.idWorkitem==idWorkitem;
                });
                var dataForModal = {
                    users: usersForModal,
                    isActivity: task.type==="activity",
                    activityName: task.displayName
                };
                self.publish("showDialogWidget", {
                    widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_USERS_MODAL,
                    closeVisible: true,
                    data: dataForModal,
                    maximize: true,
                    modalParameters: {
                        title: bizagi.localization.getResource("workportal-widget-timeline-show-more-users-modal-title")+ " ("+usersForModal.length+")",
                        width: 790,
                        height: 526,
                        refreshInbox: false
                    }
                });
            });
        });
    },

    /**
     * get the users from usersInformation for the modal but if needs more get it from userPictures endpoint
     * @param {widget.project.timeline} self 
     * @param {Array} userListAItems id users for the modal
     */
    getUsersForUnassignedActivityModal: function(self, userListAItems){
        var defer = $.Deferred();
        var usersForModal = [];
        var usersMissingInformation = [];
        userListAItems.map(function (u) {
            var userFounded=self.usersInformation.find(function (u2) {
                return u.guidUser==u2.id;
            });
            if (userFounded===undefined) {
                usersMissingInformation.push(u.guidUser);
            } else {
                usersForModal.push(userFounded);
            }
        });
        if (usersMissingInformation.length == 0) {// has all users information needed
           defer.resolve(self.prepareUsersForModal(usersForModal)); 
        } else {//look for missing user information
            $.when(self.getUsersData(usersMissingInformation.join(","))).done(function (rawData) {
                defer.resolve(self.prepareUsersForModal(usersForModal.concat(rawData))); 
            });
        }
        
        return defer;
    },

    /**
     * apply the userAdapter standard for the userModal
     * @param {Array} usersForModal users for the modal but missing userAdapter standars
     */
    prepareUsersForModal: function(usersForModal){
        var usersReady = [];
        usersForModal.map(function(u) {
            var initials=u.initials==undefined?u.name.getInitials():u.initials;
            var user=usersAdapter.createJsonUserInfo(u.id, u.name, u.username, u.picture, u.email, initials, null, null, [], []);
            usersReady.push(user);
        });
        return usersReady;
    },

    /*
     * Renders the template defined in the widget
     */
    renderContent: function() {
        var self = this;
        var template = self.getTemplate("project-timeline-markup");

        // Define initial filters
        self.viewProcesses = false;
        self.viewActivities = true;

        //
        self.currentDate = self.getDataObj().date;

        var content = self.content = template.render({ viewProcesses: self.viewProcesses, viewActivities: self.viewActivities, showCurrentDate: self.showCurrentDate, date:self.currentDate});
        var container = $("#bz-project-timeline-activities", self.content);

        // Draw activities
        self.renderActivities(container);

        // Search option handlers
        $(".bz-project-timeline-search .bz-icon-search", content).on("click", function () {
            var textToSearch = $(".bz-project-timeline-search input", content).val();
            self.filterActivities(container, textToSearch);
        });

        $(".bz-project-timeline-search input", content).change(function () {
            var textToSearch = $(".bz-project-timeline-search input", content).val();
            self.filterActivities(container, textToSearch);
        }).keypress(function(e){
            // keypress event ensures enter key to action the below implementation on IE browser.
            if(bizagi.util.isIE() && ((e.keyCode || e.which)  == 13) ){
                var textToSearch = $(".bz-project-timeline-search input", content).val();
                self.filterActivities(container, textToSearch);
            }
        });

        return self.content;
    },

    renderActivities: function (container) {
        var self = this;
        var content = self.getContent();
        container.empty();

        // Define helper functions
        var helper = {
            getFormattedDate: self.getFormattedDate,
            getFormattedTime: self.getFormattedTime,
            getPastEvents: self.getPastEvents,
            getCurrentEvents: self.getCurrentEvents,
            canShowActivity: $.proxy(self, 'canShowActivity'),
            canShowGraphicQuery: $.proxy(self, 'canShowGraphicQuery'),
            canShowStateLog: $.proxy(self, 'canShowStateLog')
        };

        // Draw timeline
        $.when(self.getRadNumber(self.radNumber)).done(function(radNumber){
            $.when(self.getTimelineData(radNumber))
                .done(function (data) {
                    
                    if (data.events.length > 0) {
                        
                        self.numberOfResults = 0;
                        var template = self.getTemplate("project-timeline-single-process");
                        var templateRender = template.render($.extend(data, {security: bizagi.menuSecurity}), helper); //Template with results
                      
                        if(self.numberOfResults === 0){ //If there is no results it is shown a message telling that (the variable numberOfResults is incremented in the canShowActivity method)
                            var templateNoResults = self.getTemplate("project-timeline-empty-results");
                            container.append(templateNoResults.render($.extend(data, {security: bizagi.menuSecurity}), helper));
                        }else{ //In the other case, the activities in the timeline are shown
                            container.append(templateRender);
                        }
                        
                        // Add handlers
                        $(".bz-more-users-link", content).click(function(e) {
                            self.showMoreUsersModal(self, e);
                        });

                        $(".bz-project-timeline-type-activity .bz-project-timeline-content h2, .bz-project-timeline-type-event .bz-project-timeline-content h2", container).on("click", function () {
                            var idCase = $(this).closest(".bz-project-timeline-content").data("case");
                            self.showGlobalForm(idCase);
                        });

                        $(".bz-project-timeline-content .bz-show-diagrams", container).on("click", function () {
                            var idCase = $(this).closest(".bz-project-timeline-content").data("case");
                            var idWorkflow = $(this).closest(".bz-project-timeline-content").data("workflow");
                            self.showGraphicQuery({ idCase: idCase, idWorkflow: idWorkflow });
                        });

                        $(".bz-project-timeline-content .bz-show-log", container).on("click", function () {
                            var idCase = $(this).closest(".bz-project-timeline-content").data("case");
                            bizagi.loader.startAndThen("activity").then(function () {
                                self.showLog(idCase);
                            });
                        });

                        $(".bz-project-timeline-content .bz-show-case", container).on("click", function (event) {
                            //event.preventDefault();
                            // Call routing action
                            var idCase = $(this).closest(".bz-project-timeline-content").data("case");
                            var idWorkItem = $(this).closest(".bz-project-timeline-content").data("workitem");
                            var idWorkflow = $(this).closest(".bz-project-timeline-content").data("workflow");
                    
                            self.publish("closeAllDialogs");
                            self.publish("executeAction", {
                                action: bizagi.workportal.actions.action.BIZAGI_WORKPORTAL_ACTION_ROUTING,
                                idCase: idCase,
                                idWorkItem: idWorkItem,
                                idTask: "",
                                idWorkflow: idWorkflow,
                                eventAsTasks: true,
                                onlyUserWorkItems: "true"
                            });
                         });

                        // Add animation
                        $("#bz-project-timeline-content", content).addClass("bz-project-timeline-content-bounce");

                        // Render assignees
                        self.renderAssignees(container);

                    } else {
                        self.showEmptyTimelineTemplate(container, data, helper, content, bizagi.menuSecurity);
                    }
                });
        });
    },

    /**
     * show timeline empty template
     * @param {Jquery Element} container for template to create
     * @param {JSON} data response from timeline endpoint
     * @param {JSON functions} helper for template methods
     * @param {Jquery Element} content 
     * @param {JSON} menuSecurity for template
     */
    showEmptyTimelineTemplate: function(container, data, helper, content, menuSecurity){
        var self = this,
            templateContent = self.getEmptyTimelineContent(data, helper, menuSecurity);
        container.append(templateContent);
        self.adjustElementsForEmptyTimeline(content);
    },

    /**
     * return timeline template empty and the message depends of data.message value
     * @param {JSON} data response from timeline endpoint
     * @param {JSON functions} helper for template methods
     * @param {JSON} menuSecurity for template
     */
    getEmptyTimelineContent: function(data, helper, menuSecurity){
        var self = this,
            template = self.getTemplate("project-timeline-empty-process"),
            getMessages = function(customMessage) {
                var originalFirst = bizagi.localization.getResource("workportal-general-first-line-not-find-a-historical-record"),
                    originalSecond = bizagi.localization.getResource("workportal-general-second-line-not-find-a-historical-record"),
                    first = customMessage ? customMessage : originalFirst,
                    second = customMessage ? "" : originalSecond;
                return {
                    first: first,
                    second: second
                };
            },
            extendData = {
                security: menuSecurity,
                messages: getMessages(data.message)
            },
            dataTemplate = $.extend(data, extendData);
        return template.render(dataTemplate, helper);
    },

    /**
     * adjust the HTML Elements for empty timeline
     * @param {Jquery Element} content 
     */
    adjustElementsForEmptyTimeline: function(content){
        $(".bz-project-timeline-search", content).css("display", "none");
        $("#bz-project-timeline-content", content).css("visibility", "visible");
    },

    /**
     * Get idCase by if case is contextualized or not
     * @param value
     * @returns {*}
     */
    getIdCase: function(value){
        var self = this,
            defer = $.Deferred();
        if(bizagi.util.isGUID(value)){
            $.when(self.dataService.getCaseByPlan({idPlan: value})).done(function(response){
                defer.resolve(response.id);
            });
        }
        else{
            if (self.params.idCase===undefined && self.params.plan.contextualized===true) {
                self.params.idCase=self.params.plan.parent.idCase;
            }
            defer.resolve(self.params.idCase);
        }
        return defer.promise();
    },

    /**
     * Get Rad Number. Validate if is contextualized activity
     * @param value
     * @returns {*}
     */
    getRadNumber: function(value){
        var self = this,
            defer = $.Deferred();

        if(typeof value === "undefined"){
            value = self.params.plan.id;
        }

        if(bizagi.util.isGUID(value)){
            value = self.params.plan.id;
            $.when(self.dataService.getCaseByPlan({idPlan: value})).done(function(response){
                defer.resolve("idCase=" + response.id);
            });
        }
        else{
            defer.resolve("radNumber=" + encodeURIComponent(value));
        }
        return defer.promise();
    },

    showGlobalForm: function (idCase) {
        var dialog = bizagi.injector.get("dialogWidgets");
        var data = {
            closeVisible: true,
            modalParameters: {
                title: bizagi.localization.getResource("workportal-project-casedashboard-overview"),
                refreshInbox: false
            },
            idCase: idCase
        };
        data.widgetInstance = bizagi.injector.get("bizagi.workportal.widgets.project.overview", data);
        data.widgetInstance.sub("closeDialog", function (ev, data) {
            dialog.close();
            defer.resolve(data);
        });

        dialog.renderWidget(data);
    },

    showLog: function (idCase) {
        var dialog = bizagi.injector.get("dialogWidgets");
        var data = {
            closeVisible: true,
            modalParameters: {
                title: bizagi.localization.getResource("workportal-project-casedashboard-history"),
                refreshInbox: false
            },
            idCase: idCase
        };
        data.widgetInstance = bizagi.injector.get("bizagi.workportal.widgets.activity.log", data);
        data.widgetInstance.sub("closeDialog", function (ev, data) {
            dialog.close();
            defer.resolve(data);
        });

        dialog.renderWidget(data);
    },

    renderAssignees: function (container) {
        var self = this;
        var assignees = {};
        $.each($(".bz-assignee", container), function (i, item) {
            var assignee = $(item).data("guidassignee");
            assignees[assignee] = assignee;
        });

        // Render assignee pictures
        self.renderAssigneePictures(Object.keys(assignees), container);
    },

    renderAssigneePictures: function (users, container, guidUsers) {
        var self = this;
        var assigneeTemplate = self.getTemplate("project-timeline-activity-assignee");
        var helper = {
            getInitials: function (name) { return name.getInitials(); }
        };
        if (guidUsers == undefined) {
            guidUsers = users;
        }

        // Check in hash first
        var allUsersInCache = true;
        var cachedKeys = $.map(self.userPictures, function (user) { return user.id; });
        $.each(users, function (i, user) { if ($.inArray(Number(user), cachedKeys) === -1) allUsersInCache = false; });

        // Go for cache if everything is there
        if (allUsersInCache) {
            $.each(users, function(i, user) {
                $(".bz-assignee[data-guidassignee=" + user + "]", container).append(assigneeTemplate.render(self.userPictures[user], helper));
            });
            return;
        }

        // Draw assignee pictures
        $.when(self.dataService.getAssigneePicture(guidUsers))
        .done(function (data) {
            self.joinUsersInformation(data);
            $.each(data, function (i, item) {
                var assigned=false;
                self.timelineData.events.map(function(event) {
                    assigned |= event.status == "open" && event.assignee == item.id;
                });
                if (assigned) {
                    item.types = item.types || [];
                    item.types.push("assigned");
                }
                $(".bz-assignee[data-guidassignee=" + item.id + "]", container).append(assigneeTemplate.render(item, helper));
                self.usersByName[item.name] = item.id;
                self.userPictures[item.id] = item;
            });
            self.addEventHandlersTooltip(data, container);
        });
    },

    /**
     * join the users information into self.usersInformation, used for show all usertooltips
     * @param {JSON} users 
     */
    joinUsersInformation: function(users) {
        var self=this;
        if(self.usersInformation==undefined)self.usersInformation=[];
        users.map(function(u) {
            var userFounded=false;
            self.usersInformation.map(function(u2) {
                if (u2.id==u.id) {
                    userFounded=true;
                    u2=$.extend(u2, u);
                }
            });
            if (userFounded==false) {
                //if don't know if the user is owner or assignee
                if (u.types==undefined) {
                    u.types=[];
                }
                if (u.initials==undefined) {
                    u.initials=u.name.getInitials();
                }
                self.usersInformation.push(u);
            }
        });
    },

    //Add event handler for tooltip
    addEventHandlersTooltip: function(data, container){
        var self = this;
        var typesToAssignUsers = [];

        
        $.when(self.getIdCase(self.radNumber)).done(function (idCase) {
            self.dataService.getCaseSummary({ idCase: idCase }).done(function (summary) {
                typesToAssignUsers = [{
                    id: summary.createdBy.userId,
                    typeName: "owner"
                }];

                self.dataService.getCaseAssignees({ idCase: idCase }).done(function (dataAssignedUser) {
                    var csvUserGuids = self.getCSVUserGuids(dataAssignedUser.assignees, typesToAssignUsers);
                    $.when(self.getUsersData(csvUserGuids)).done(function (rawData) {
                        var preparedData = self.prepareData(rawData, typesToAssignUsers, dataAssignedUser);
                        self.joinUsersInformation(preparedData);
                    });
                });
            });            
        });

        $(".bz-project-timeline-content > .bz-assignee", self.content).on('mouseenter', '', function (ev) {
            var $target = $(ev.target);
            var id_userSelected=$target.closest('.bz-assignee').attr('data-assignee');
            var user=self.usersInformation.find(function(user) {
                return user.id==id_userSelected;
            });
            if(user && user.id==id_userSelected){
                if(user.types===undefined || user.types.length===0){
                    user.owner = false;
                    user.isAssignee = false;
                }else
                user.types.forEach(function(type){
                    if(type == "owner"){
                        user.owner = true
                    }
                    user.isAssignee = type.toLowerCase() == "assigned";
                });
                var userParam=usersAdapter.createJsonUserInfo(user.id, user.name, user.username, user.picture?"data:image/png;base64,"+user.picture:undefined, user.email, user.initials, user.isAssignee, user.owner, [], []);
                $target.parent().usertooltip(self,{target:$target,user:userParam});
            }
        });
    },

    
    /**
     *
     * @param assignees
     * @param typesToAssignUsers
     * @returns {*}
     */
    getCSVUserGuids: function (assignees, typesToAssignUsers) {
        var arrayIdsUsers = $.map(assignees, function (itemUser) {
            return itemUser.idUser;
        });
        arrayIdsUsers.unshift(typesToAssignUsers[0].id);
        return arrayIdsUsers.join(",");
    },

    /**
    * Get data users with service
    */
    getUsersData: function (csvguidUsers) {
        var self = this;
        var defer = $.Deferred();
        var params = {
            usersGuids: csvguidUsers,
            width: 50,
            height: 50
        };

        self.dataService.getUsersData(params).done(function (response) {
            defer.resolve(response);
        }).fail(function(){
            defer.resolve([]);
        });

        return defer.promise();
    },

    /**
    *
    * @param typesToAssignUsers
    * @param dataAssignedUser
    */
    mergeTypesToAssignUsers: function (typesToAssignUsers, dataAssignedUser) {
        var self = this;
        for (var i = 0, totalDataAssigned = dataAssignedUser.assignees.length; i < totalDataAssigned; i++) {
            //Using the hash, validate no repeat elements on typesToAssignUsers
            var temp = self.auxNoRepeatTypesUser[dataAssignedUser.assignees[i].idUser.toString() + "assigned"];
            if (typeof temp === "undefined") {
                self.auxNoRepeatTypesUser[dataAssignedUser.assignees[i].idUser.toString() + "assigned"] = "";
                typesToAssignUsers.push({
                    id: dataAssignedUser.assignees[i].idUser || -1,
                    typeName: "assigned"
                });
            }
        }
    },

    prepareData: function (rawUsersData, typesToAssignUsers, dataAssignedUser) {
        var self = this;
        var dataProcessed = [];
        if (rawUsersData) {
            self.mergeTypesToAssignUsers(typesToAssignUsers, dataAssignedUser);
            for (var i = 0, j = rawUsersData.length; i < j; i++) {
                var user = self.prepareDataUser(rawUsersData[i], typesToAssignUsers, dataAssignedUser);
                dataProcessed.push(user);
            }
        }
        return dataProcessed;
    },

    /**
    * Prepare data by user
    */
    prepareDataUser: function (rawDataUser, typesToAssignUsers, dataAssignedUsers) {
        var self = this;
        var id = rawDataUser.id;
        var user = {
            id: id,
            name: rawDataUser.name,
            username: rawDataUser.username,
            email: rawDataUser.email,
            initials: rawDataUser.name.getInitials(),
            picture: (rawDataUser.picture) ? "data:image/png;base64," + rawDataUser.picture : undefined,
            types: self.assignUserTypes(id.toString(), typesToAssignUsers),
            tasks: self.getTasksUser(dataAssignedUsers, id.toString())
        };

        user.typesString = $.map(user.types, function(type) {
            return self.hashTypesUser[type];
        }).join(",");

        return user;
    },

    /**
    *
    * @param dataAssignedUsers
    * @param idUser
    * @returns {Array}
    */
    getTasksUser: function(dataAssignedUsers, idUser){
        var tasks = [];
        var dataAssignedUser = dataAssignedUsers.assignees.filter(function(user){
            return user.idUser.toString() === idUser;
        });

        for (var i = 0, total = dataAssignedUser.length; i < total; i += 1) {
            tasks.push({
                taskType: bizagi.util.parseBoolean(dataAssignedUser[i].isEvent) ? bizagi.localization.getResource("workportal-project-user-event"): bizagi.localization.getResource("workportal-project-user-task"),
                taskName: dataAssignedUser[i].taskName
            });
        }
        return tasks;
    },

    /**
     *
     * @param userId
     * @param typesToAssignUsers
     * @returns {Array}
     */
    assignUserTypes: function(userId, typesToAssignUsers){
        var typesUser = [];

        for (var i = 0, totalTypeUser = typesToAssignUsers.length;i < totalTypeUser; i += 1){
            if (userId === typesToAssignUsers[i].id.toString()) {
                typesUser.push(typesToAssignUsers[i].typeName);
            }
        }

        return typesUser;
    },

    filterActivities: function (container, textToSearch) {
        var self = this;
        self.filter = textToSearch.trim().toLowerCase();

        // Draw activities again
        self.renderActivities(container);
    },

    getTimelineData: function(radNumber) {
        var self = this;
        if (self.timelineData) {
            return $.Deferred().resolve(self.timelineData);
        }

        return $.when(self.dataService.getTimeLineActivities(radNumber))
                .done(function(data) {
                    self.timelineData = data;
                });
    },

    /* HELPER FUNCTIONS */
    getFormattedDate: function (timestamp) {
        var dateObj = new Date(timestamp);
        var monthsNames = bizagi.localization.getResource("datePickerRegional").monthNames;
        var shortMonth = dateObj.getMonth();
        return monthsNames[shortMonth] + " " + bizagi.util.dateFormatter.formatDate(dateObj, "d yyyy");
    },
    getFormattedTime: function (timestamp) {
        var date = new Date(timestamp);
        return bizagi.util.dateFormatter.formatDate(date, "HH:mm");
    },

    getPastEvents: function(events) {
        return $.grep(events, function (event) { return event.date != null; });
    },

    getCurrentEvents: function (events) {
        return $.grep(events, function (event) { return event.date == null; });
    },

    canShowActivity: function(activity) {
        /**
         * When this method returns true (it means the activity will be shown or not according to the filter applied) 
         * the variable numberOfResult increments to get the total number of results (activities) that will be shown, 
         * in order to show a message when there is no results
         * (this method is executed for each activity of the timeline)
         */
        //
        var self = this;
        if (!self.filter){
            self.numberOfResults = self.numberOfResults + 1;
            return true;} 
        if (self.filter === ""){
            self.numberOfResults = self.numberOfResults + 1;
            return true;} 
        if (activity.displayName.toLowerCase().indexOf(self.filter) > -1){
            self.numberOfResults = self.numberOfResults + 1;
            return true;
        } 

        // Filter by user name
        var filteredUsers = $.grep(Object.keys(self.usersByName), function (user) { return user.toLowerCase().indexOf(self.filter) > -1; });
        var validValues = $.map(filteredUsers, function (user) { return self.usersByName[user]; });
        if ($.inArray(activity.assignee, validValues) > -1) {
            self.numberOfResults = self.numberOfResults + 1;
            return true;
        }
        return false;
    },

    canShowGraphicQuery: function() {
        var result = false;
        if(bizagi.menuSecurity.GraphicQuery){
            result = true;
        }
        return result;
    },

    canShowStateLog: function() {
        var result = false;
        if(bizagi.menuSecurity.StateLog){
            result = true;
        }
        return result;
    },
    getDataObj: function () {
        var self = this,
            regional = self.getResource("datePickerRegional"),
            currentDate = new Date(),
            currentDay = regional.dayNames[currentDate.getDay()],
            currentMonth = regional.monthNames[currentDate.getMonth()],
            date,
            storage = bizagi.services.preferences().getPreferences() || {};
        
        date = currentDay + ", " + currentMonth + " " + currentDate.getDate() + ", " + currentDate.getFullYear()
        if(storage.longDateFormat)
            date = bizagi.util.dateFormatter.formatDate(currentDate, storage.longDateFormat);

        return {
            "date":date
        };
    }
});

bizagi.injector.register("bizagi.workportal.widgets.project.timeline", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.timeline], true);
